@import url("https://fonts.googleapis.com/css2?family=Almarai:wght@300;400;700;800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

*{
  font-family: "Poppins", sans-serif;
}

.formContainer {
  background: linear-gradient(174deg, #319e7a, #059aa5);
  background-size: 400% 400%;
  animation: grad 10s ease infinite;
  
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
 
  @keyframes grad {
    0%{background-position:53% 0%}
    50%{background-position:48% 100%}
    100%{background-position:53% 0%}
  }


  .formWrapper {
    background-color: rgb(255, 255, 255);
    padding: 20px 60px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    .logo {
      color: #3fc99b;
      font-weight: bold;
      font-size: 24px;
    }
    .title{
      color: #696969;
    }
    form {
      display: flex;
      flex-direction: column;
      gap: 15px;
      
      input{
        padding: 15px;
        border: none;
        border-bottom: 1px solid #838383;
        width: 250px;
        &::placeholder{
          color: #8f8f8f;
          font-size: 15px;
          
        }
      }

      .btn{
        font-family: 'Poppins','Almarai', sans-serif;
        display: block;
        width: 100%;
        height: 3rem;
        background-color: rgb(15, 23, 42);
        margin-left: auto;
        margin-right: auto;
        border: none;
        font-size: 1.2rem;
        line-height: 1;
        text-align: center;
        
        color:white;
        
        cursor: pointer;
        color: #fff;
        background-color: #3fc99b;
        border-radius: 55px;
        box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
        transition: all 0.4s ease 0s;
        outline: none;
      }
      .btn:focus{
        outline:none;
        box-shadow: none;
      }
      .btn:hover{
        transition: all 0.4s ease-in-out;
        background-color: #2b8a6a;
        box-shadow: 0px 5px 10px rgba(7, 14, 12, 0.4);
        color: #ffffff;
        
      }

      label{
        display: flex;
        align-items: center;
        gap:10px;
        cursor: pointer;
      }
      
    }
    p{
      color: #5f5f5f;
      font-size: 15px;
      margin-top: 10px;
    }
  }
}
